import React, { Component } from 'react';
import { func, object } from 'prop-types';
import { navigateTo } from 'gatsby-link';
import TopBar from '../components/Navigation/TopBar';
import Footer from '../components/Footer';
import GoogleAnalytics from '../components/Analytics/Google';
import YandexMetrica from '../components/Analytics/Yandex';
import SubscriptionForm from '../components/Subscription/SubscriptionForm';
import SendPulse from '../components/Subscription/SendPulse';
import MailChimp from '../components/Subscription/MailChimp';
import '../css/style.css';

export default class Template extends Component {
  static propTypes = {
    children: func,
    location: object,
    route: object,
  };

  navigateTo = ({ target }) => {
    navigateTo(target.href.replace(document.location.origin, ''));
  };

  render() {
    const { children } = this.props;

    return (
      <div className="layout">
        <TopBar />
        {children()}
        <Footer />
        <GoogleAnalytics id="UA-60230177-1" />
        <YandexMetrica id="28756646" />
        <SendPulse />
      </div>
    );
  }
}


