import React, { Component } from 'react';
import { findDOMNode } from 'react-dom';
import Link from 'gatsby-link';
import classnames from 'classnames';
import ToolBar from '../Menu/ToolBar';
import EventListener from 'react-helpful/EventListener';
import { siteUrl } from '../../../config.json';

const menuItems = [
  {
    title: '',
    route: '/',
    icon: 'code',
  },
  {
    title: '',
    route: '/',
    icon: 'videogame_asset',
  },
  {
    title: 'DIY',
    route: '/diy',
    icon: 'lightbulb_outline',
  },
  {
    title: '',
    route: '/',
    icon: 'flight',
    iconClass: 'rotate-90'
  },
  {
    title: '',
    route: '/',
    icon: 'headset_mic',
  },
];

export default class TopBar extends Component {

  state = {
    showDrawer: false,
    showMenu: false,
    menuAnchor: null,
  };

  goSocial = () => {
    this.setState({ showMenu: false });
  };

  toggleMenu = () => {
    this.setState({ showMenu: !this.state.showMenu });
  };

  clickHandler = () => {
    if (this.state.showMenu) {
      this.setState({ showMenu: false });
    }
  };

  resizeHandler = () => {
    this.setState({ showMenu: false });
  };

  render() {
    return (
      <nav className="top-bar">
        <div className="top-bar__header">
          <Link to="/">
            Pot of Code
          </Link>
          <button className="hide-sm-up top-menu__button" onClickCapture={this.toggleMenu}><i className="material-icons">menu</i></button>
        </div>
        <EventListener
          target={() => document}
          event="click"
          excludeParents={['.top-menu', '.top-menu__button']}
          on={this.clickHandler}
        />
        <EventListener
          target={() => window}
          event="resize"
          on={this.resizeHandler}
        />
        <ToolBar items={menuItems} className={classnames('top-menu', { 'top-menu--show': this.state.showMenu })} onClick={this.toggleMenu} />
        <div hidden>
          <div onClick={this.goSocial} data-url="https://facebook.com/3axap4eHko"><i className="i-dark fa fa-facebook fa-2x" /></div>
          <div onClick={this.goSocial} data-url="https://facebook.com/3axap4eHko"><i className="i-dark fa fa-steam fa-2x" /></div>
          <div onClick={this.goSocial} data-url="https://facebook.com/3axap4eHko"><i className="i-dark fa fa-twitch fa-2x" /></div>
          <div onClick={this.goSocial} data-url="https://facebook.com/3axap4eHko"><i className="i-dark fa fa-vk fa-2x" /></div>
          <div onClick={this.goSocial} data-url="https://facebook.com/3axap4eHko"><i className="i-dark fa fa-twitter fa-2x" /></div>
          <div onClick={this.goSocial} data-url="https://facebook.com/3axap4eHko"><i className="i-dark fa fa-google-plus fa-2x" /></div>
          <div onClick={this.goSocial} data-url="https://facebook.com/3axap4eHko"><i className="i-dark fa fa-linkedin fa-2x" /></div>
        </div>
      </nav>
    );
  }
}



