import React, { Component } from 'react';
import Link from 'gatsby-link';
import classnames from 'classnames';

class ToolBarItem extends Component {
  render() {
    const { title, route, icon, iconClass, onClick } = this.props;
    return <li className="tool-bar__item">
      <Link to={route} className="tool-bar__link" onClick={onClick} activeClassName="active">
        <i className={classnames('material-icons tool-bar__icon', iconClass)}>{icon}</i>
        {title}
      </Link>
    </li>;
  }
}

export default class ToolBar extends Component {
  render() {
    const { items, onClick, className } = this.props;
    return (
      <ul className={classnames('tool-bar', className)}>
        {items.map(item => <ToolBarItem key={item.title} {...item} onClick={onClick} />)}
      </ul>
    );
  }
}



