import React, { Component } from 'react';
import {} from 'prop-types';
import Overlay from '../UI/Overlay';

export default class SubscriptionForm extends Component {
  static defaultProps = {
    lang: 'ru',
    hash: '5282c330c9f7c6a127f710619146a2659e7cdac1364771397b45e43c555ffd02',
  };

  state = {
    email: '',
    display: false,
  };

  onSubscribeResponse = (...args) => {
    debugger;
  };

  onSubscribe = event => {
    event.preventDefault();
    const { lang, hash } = this.props;
    const { email } = this.state;

    const params = {
      callback: 'onSubscribeResponse',
      sform_lang: lang,
      'sform[hash]': hash,
      'sform[email]': email,
      [`sform[${btoa('autoSite')}]`]: 'potofcode.com',
      _: Date.now(),
    };
    const query = Object.entries(params).map(([key, value]) => encodeURIComponent(key) + '=' + encodeURIComponent(value)).join('&');
    const src = `https://login.sendpulse.com/members/forms/jsonp-submit?${query}`;
    const script = document.createElement('script');
    script.src = src;
    script.onload = () => script.parentNode.removeChild(script);
    document.body.appendChild(script);
  };

  onEmailChange = ({ target: { value } }) => {
    this.setState({ email: value });
  };

  onCloseRequest = () => {
    this.setState({ display: false });
  };

  componentDidMount() {
    window.onSubscribeResponse = this.onSubscribeResponse;
    this.setState({ display: true });
  }

  render() {
    const { display, email } = this.state;
    return (
      <Overlay display={display} onCloseRequest={this.onCloseRequest} transition="slide-up">
        <div className="subscribe">
          <div className="subscribe__container">
            <div className="subscribe__close" onClick={this.onCloseRequest}>close</div>
            <div className="subscribe__title">
              <div className="ribbon">
                <div className="ribbon-stitches-top" />
                <div className="ribbon-content">
                  <h1><b>
                    Pot of Code<br />
                     <br />
                  </b></h1>
                </div>
                <div className="ribbon-stitches-bottom" />
              </div>
            </div>
            <input
              type="email"
              className="subscribe__email"
              placeholder="username@gmail.com"
              value={email}
              onChange={this.onEmailChange}
              autoFocus={true}
              required="required"
            />
            <button className="subscribe__button" onClick={this.onSubscribe}></button>
          </div>
        </div>
      </Overlay>
    );
  }
}



