import React, { Component } from 'react';
import Link from 'gatsby-link';
import { Row, Col } from './UI/Grid';


const socials = [
  { link: 'https://facebook.com/3axap4eHko', icon: 'fa-facebook', title: 'Facebook' },
  { link: 'https://steamcommunity.com/id/3axap4eHko/', icon: 'fa-steam', title: 'Steam' },
  { link: 'https://www.twitch.tv/3axap4ehko', icon: 'fa-twitch', title: 'Twitch' },
  { link: 'https://vk.com/Zaxap4ehko', icon: 'fa-vk', title: 'VKontakte' },
  { link: 'https://twitter.com/3axap4eHko', icon: 'fa-twitter', title: 'Twitter' },
  { link: 'https://github.com/3axap4eHko', icon: 'fa-github', title: 'Github' },
  { link: 'https://www.linkedin.com/in/3axap4ehko/en', icon: 'fa-linkedin', title: 'Linkedin' },
  { link: 'https://plus.google.com/+IvanZakharchenko', icon: 'fa-google-plus', title: 'Google Plus' },
];

export default class Footer extends Component {
  render() {
    return (
      <footer className="footer">
        <Row className="social footer__follow">
          <Col xs="12" lg="3" className="footer__follow-title"> </Col>
          {socials.map(({ link, icon, title}) => <Col xs="1" key={title}>
            <a href={link} target={title} className={`i-dark fa ${icon} fa-2x`} title={title} aria-label={`   ${title}`} />
          </Col>)}
        </Row>
        <Row className="footer__info">
          <Col xs={12} md={4}>
            <a href="https://potofcode.com/" className="link footer__logo">POT OF CODE</a>
          </Col>
          <Col xs={12} md={8}>
            <Row>
              <Col xs={12} sm={4}>
                <Link to="/terms" className="link" aria-label=""></Link>
              </Col>
              <Col xs={12} sm={4}>
                <a href="/sitemap.xml" className="link" target="__sitemap">Sitemap</a>
              </Col>
              <Col xs={12} sm={4}>
                <div> 2015-2017 Ivan (3axap4eHko) Zakharchenko.   </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </footer>
    );
  }
}



